
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    options: {
      type: Set as PropType<Set<string>>,
      required: true,
    },
    groupName: {
      type: String,
      required: true,
    },
    defaultSelected: {
      type: String,
      required: true,
    },
    idxOfParent: {
      type: Number,
      required: true,
    },
  },
  emits: ["updateSelection"],
  data() {
    return {
      currentSelected: this.defaultSelected as String,
    };
  },
});
